/* API Fonts */
@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900&display=swap');

/* Reset */
html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: #F5F5F5;
  overflow-x: hidden;
  /* width: 100vw; */
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
